[data-view="card"] {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  width: 54px;
  height: 54px;
  margin: 1px;
  float: left;
  cursor: pointer;
  background-color: #007bff;
  border-radius: 5px;
}


  
  .flipped {
    transition: 0.2s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    position: relative;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }
  
  .reverse {
    transition: 0.2s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    position: relative;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
  
  .correct {
    transition: 0.8s;
    opacity: 0.2;
    cursor: default;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    position: relative;
    -webkit-transform: translateX(-5000%);
            transform: translateX(-5000%);
  }
  
  /* Cards */
  .correct[data-item="1"],
  .flipped[data-item="1"] {
    background: url(/static/media/1.5e7b88f0.png) left center no-repeat;
    background-size: contain;
