[data-view="card"] {
  transform: rotateY(180deg);
  width: 54px;
  height: 54px;
  margin: 1px;
  float: left;
  cursor: pointer;
  background-color: #007bff;
  border-radius: 5px;
}


  
  .flipped {
    transition: 0.2s;
    transform-style: preserve-3d;
    position: relative;
    transform: rotateY(0deg);
  }
  
  .reverse {
    transition: 0.2s;
    transform-style: preserve-3d;
    position: relative;
    transform: rotateY(180deg);
  }
  
  .correct {
    transition: 0.8s;
    opacity: 0.2;
    cursor: default;
    transform-style: preserve-3d;
    position: relative;
    transform: translateX(-5000%);
  }
  
  /* Cards */
  .correct[data-item="1"],
  .flipped[data-item="1"] {
    background: url("../img/1.png") left center no-repeat;
    background-size: contain;
  }
  
